/**
 * stice
 */
import {TokenAnalyzer} from './token-analyzer';
import {TK_PART, TK_UNKNOWN, Token} from '../model/analysis-result';

export class Parts
  implements TokenAnalyzer {
  parts: string[] = ["a", "k", "nejsp", "zajist", "prost", "snad", "asi", "prv", "mon", "prv", "jen", "tak", "ano", "ne", "nikoli", "nikoliv", "samozejm", "bohuel", "kadopdn", "natst", "pli", "velmi", "velice", "pevelice", "moc", "teba", "teprve", "teprv", "pouze", "pmo"];

  analyze(token: Token) {
    if (token.kind !== TK_UNKNOWN) {
      return;
    }
    const text: string = token.text.toLowerCase().trim();
    for (const c of this.parts) {
      if (c === text) {
        token.kind = TK_PART;
      }
    }
  }
}
