import {TokenAnalyzer} from './token-analyzer';
import {TK_EMPTY_LINE, Token} from '../model/analysis-result';

/**
 * Detekce przdnch dk
 */
export class EmptyLineDetector
  implements TokenAnalyzer {

  analyze(token: Token) {
    if (token.text.trim() === '') {
      token.kind = TK_EMPTY_LINE;
    }
  }

}
